//  
import theAxios from 'axios'
import router from '@/router'
import Notify from '@/ui/Notify.js'
import { getToken, removeToken } from './token' // setToken
// import { getNewTokenAPI } from '@/api'

const axios = theAxios.create({
  baseURL: 'http://toutiao.itheima.net',
  // baseURL: 'http://geek.itheima.net',
  timeout: 20000
})

// 
axios.interceptors.request.use(
  function (config) {
    // 
    // token
    // ?. lengthundefind
    // console.log(config)
    if (getToken()?.length > 0 && config.headers.Authorization === undefined) {
      config.headers.Authorization = `Bearer ${getToken()}`
    }
    return config
  },
  function (error) {
    // 
    return Promise.reject(error)
  }
)

// 
axios.interceptors.response.use(
  function (response) {
    // 2xx 
    // 
    return response
  },
  async function (error) {
    //  2xx 
    // 
    // 401
    if (error.response.status === 401) {
      // Notify({ type: 'warning', message: '' })

      // 1token,
      removeToken() // token
      // router.replace('/login')

      // 2token
      //   const res = await getNewTokenAPI()
      //   console.log(res)
      //   setToken(res.data.data.token)
      //   error.config.headers.Authorization = `Bearer ${res.data.data.token}`
      //   // 
      //   return axios(error.config)
      // } else if (error.response.status === 500 && error.config.url ===
      //   '/v1_0/authorizations' && error.config.method === 'put') { // refresh_token
      //   Notify({ type: 'warning', message: '' })
      //   localStorage.clear() // token
      //   router.replace('/login')

      // 3token
      Notify({ type: 'warning', message: '' })
      // router.currentRoute  vuethis.$route ->
      router.replace(`/login?path=${router.currentRoute.fullPath}`)
    }

    return Promise.reject(error)
  }
)

export default ({
  url,
  method = 'GET',
  params = {},
  data = {},
  headers = {}
}) => {
  return axios({
    url,
    method,
    params,
    data,
    headers
  })
}
